<template>
  <!-- 日利率(0/000)=年利率(%)/360=月利率(%.)/30 -->
  <!-- 月利率(%.)=年利率(%)/12 -->
  <view class="day">
    <van-cell-group>
      <van-field
        model:value="{{ yRate }}"
        label="年利率"
        placeholder="请输入年利率"
        :border="{{ false }}"
        clearable
        input-align="right"
        @change="inputChangeHandle('yRate')"
      />
      <van-field
        model:value="{{ mRate }}"
        label="月利率"
        placeholder="请输入月利率"
        :border="{{ false }}"
        clearable
        input-align="right"
        @change="inputChangeHandle('mRate')"
      />
    </van-cell-group>
  </view>
</template>

<script>
import { routes } from "./../../assets/data/public.data.js";
import { getRouteTitle } from "./../../assets/function/tool.js";

export default {
  data() {
    return {
      routes,

      yRate: "111",
      mRate: "",
    };
  },

  onLoad() {
    const routes = getCurrentPages();
    const curRoute = routes[routes.length - 1].route;
    uni.setNavigationBarTitle({ title: getRouteTitle(this.routes, curRoute) });
  },

  methods: {
    inputChangeHandle(str) {
      // 数据输入时，数据反向清空
      const map = ["yRate", "mRate"];
      const arr = map.filter((item) => item !== str);
      arr.forEach((item) => {
        this[item] = "";
        console.log(this[item]);
      });

      console.log(this.yRate, this.mRate);
    },
  },
};
</script>

<style lang="scss" scoped>
.day {
  padding: 2vw 5vw;
  min-width: 100vw;
  min-height: 100vh;
  background-color: $uni-bg-color-grey;
  box-sizing: border-box;
}
</style>
